        },
        {
          label: 'FÁCIL REDECORAR',
          icon: 'dashboard',
          image: '4.jpg',
          descricao: 'Você pode trocar seu piso antigo sem grandes obras ou quebra-quebra.'
        },
        {
          label: 'PARECE MADEIRA E É MADEIRA',
          icon: 'assignment_turned_in',
          image: '5.jpg',
          descricao: 'Produzido com madeira 100% de reflorestamento, poupando recursos naturais.'
        },
        {
          label: 'CONFORTO TÉRMICO',
          icon: 'hot_tub',
          image: '6.jpg',
